import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { getLinkPath } from '../../utils'
import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../../styles/utils'
import { ImageMobile, ImageWrapper, StyledAdButton } from './commons'

const StyledAdsSection = styled.div`
  border-top: 0.5px solid ${({ theme }) => theme.colors.silver};
  padding-top: 34px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
  }
`

const StyledAdSectionButton = styled(StyledAdButton)`
  bottom: 13px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 100%;
    font-size: ${desktopVw(13)};
  }
`

const SectionImageContent = styled.div`
  position: absolute;
  top: 0;
  left: 15px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: ${mobileVw(160)};
  p {
    position: relative;
    font-size: 24px;
    font-weight: 500;
    top: 13px;
    text-transform: uppercase;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    max-width: ${desktopVw(160)};
    p {
      font-size: ${desktopVw(27)};
    }
  }
`

const SectionImageWrapper = styled(ImageWrapper)`
  margin-bottom: 33px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-basis: 50%;
    &:first-child {
      margin-right: ${desktopVw(15)};
    }
    &:last-child {
      margin-left: ${desktopVw(15)};
    }
  }
`

export function AdsSection({ sections }) {
  return (
    <StyledAdsSection>
      {sections.map(section => (
        <SectionImageWrapper>
          <ImageMobile
            fluid={section?.image?.fluid || section?.imageDesktop?.fluid}
          />
          <SectionImageContent>
            <p>
              {documentToReactComponents(
                section?.textMobile?.json || section?.text?.json,
              )}
            </p>
            <StyledAdSectionButton to={getLinkPath(section?.button)}>
              {section?.button?.text}
            </StyledAdSectionButton>
          </SectionImageContent>
        </SectionImageWrapper>
      ))}
    </StyledAdsSection>
  )
}
