import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../../styles/utils'
import { TimelineStep } from './TimelineStep'
import { colors } from '../../../styles/theme'
import { getShipmentSummary } from '../../../utils/tracking'

import StepOneIcon from '../../../assets/tracking/step_1.svg'
import StepTwoIcon from '../../../assets/tracking/step_2.svg'
import StepThreeIcon from '../../../assets/tracking/step_3.svg'
import StepFourIcon from '../../../assets/tracking/step_4.svg'

const TimelineWrapper = styled.div`
  margin-top: 25px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: 0;
    margin-bottom: -15px;
  }
`

const Status = styled.p`
  font-family: ${({ theme }) => theme.fonts.secondary};
  font-size: 35px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(25)};
  }
`

const StyledTimeLine = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: -15px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    max-width: ${desktopVw(270)};
    margin-top: ${desktopVw(-15)};
  }
`

function getActiveStep(selectedShipment) {
  switch (selectedShipment?.statusSummary?.status) {
    case 'order-accepted':
      return 0
    case 'order-dispatched':
      return 1
    case 'order-out-for-delivery':
      return 2
    case 'order-delivered':
      return 3
    case 'order-in-transit':
      return 1
    case 'order-reached-destination-country':
      return 1
    case 'order-tendered-to-agent':
      return 1
    case 'order-shipment-exception':
      return 1
    case 'order-delivery-failed':
      return 2
    case 'order-shipment-delayed':
      return 1
    default:
      return 0
  }
}

function getSvg(selectedShipment) {
  switch (selectedShipment?.statusSummary?.status) {
    case 'order-accepted':
      return <StepOneIcon />
    case 'order-dispatched':
      return <StepTwoIcon />
    case 'order-out-for-delivery':
      return <StepThreeIcon />
    case 'order-delivered':
      return <StepFourIcon />
    case 'order-in-transit':
      return <StepTwoIcon />
    default:
      return <StepOneIcon />
  }
}

export function TimeLine({ selectedShipment, shippedDate }) {
  return (
    <TimelineWrapper>
      <Status
        style={{
          color:
            selectedShipment?.statusSummary?.status === 'order-delivered'
              ? colors.shamrockGreen
              : colors.primary,
        }}
      >
        {getShipmentSummary(selectedShipment, shippedDate)}
      </Status>
      <StyledTimeLine>
        {[
          'order-accepted',
          'order-dispatched',
          'order-out-for-delivery',
          'order-delivered',
        ].map((status, currentStep) => (
          <TimelineStep
            currentStep={currentStep}
            totalSteps={4}
            activeStep={getActiveStep(selectedShipment)}
          >
            {getSvg(selectedShipment)}
          </TimelineStep>
        ))}
      </StyledTimeLine>
    </TimelineWrapper>
  )
}
