import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../../styles/utils'

const Dashed = styled.div`
  width: 100px;
  border: 1px solid
    ${({ isComplete, theme }) =>
      isComplete ? theme.colors.primary : '#C6C6C6'};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(126)};
  }
`

const Circle = styled.div`
  width: 18px;
  height: 13px;
  background: ${({ isComplete, theme }) =>
    isComplete ? theme.colors.primary : '#C6C6C6'};
  border: 1px solid
    ${({ isComplete, theme }) =>
      isComplete ? theme.colors.primary : '#C6C6C6'};
  border-radius: 50%;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 24px;
  }
`

export function TimelineStep({
  children,
  activeStep,
  totalSteps,
  currentStep,
}) {
  return (
    <>
      {currentStep === totalSteps - 1 ? (
        activeStep === currentStep ? (
          children
        ) : (
          <Circle />
        )
      ) : (
        <>
          {activeStep === currentStep ? (
            children
          ) : (
            <Circle isComplete={activeStep >= currentStep} />
          )}
          <Dashed isComplete={activeStep >= currentStep + 1} />
        </>
      )}
    </>
  )
}
