import dayjs from 'dayjs'
import { addWorkDays } from './index'

export function getTrackingRoute(order, shipment) {
  return `/tracking?orderId=${order?.orderId}&shipmentId=${shipment?.shipmentId}`
}

export function getEstimatedDelivery(selectedShipment) {
  if (selectedShipment?.statusSummary?.carrierEstimatedDeliveryDate)
    return dayjs(selectedShipment?.statusSummary?.carrierEstimatedDeliveryDate)
  if (selectedShipment?.statusSummary?.pickupDate)
    return dayjs(
      addWorkDays(
        new Date(dayjs(selectedShipment?.statusSummary?.pickupDate).format()),
        selectedShipment?.statusSummary?.deliverySla ?? 0,
      ),
    )

  const eventObj = (selectedShipment?.trackingHistory ?? []).find(
    historyItem =>
      historyItem?.lbStatus === selectedShipment?.statusSummary?.status,
  )
  if (!eventObj?.eventTime)
    return dayjs(
      addWorkDays(
        new Date(dayjs().format()),
        (selectedShipment?.statusSummary?.deliverySla ?? 0) + 1,
      ),
    )
  return dayjs(
    addWorkDays(
      new Date(dayjs(eventObj?.eventTime).format()),
      selectedShipment?.statusSummary?.deliverySla ?? 0,
    ),
  )
}

export const getLastMileTrackingUrl = (carrier, trackingDetail) => {
  if (trackingDetail.carrierTrackingUrl) {
    return trackingDetail.carrierTrackingUrl
  }
  if (trackingDetail.lastMileTrackingUrl) {
    return trackingDetail.lastMileTrackingUrl
  }
  if (
    carrier === 'JANCO_EXPRESS' &&
    trackingDetail.lastMileTrackingNumber != null
  ) {
    return `https://webtrack.dhlglobalmail.com/?trackingnumber=${trackingDetail.lastMileTrackingNumber}`
  }
  return null
}

export function getShipmentSummary(selectedShipment, shippedDate) {
  switch (selectedShipment?.statusSummary?.status) {
    case 'order-accepted':
      return 'Ready to go'
    case 'order-dispatched':
      return shippedDate
        ? `Shipped ${shippedDate.format('MMM D, dddd')}`
        : 'Shipped'
    case 'order-out-for-delivery':
      return 'Out for Delivery'
    case 'order-delivered':
      return 'Delivered'
    case 'order-in-transit':
      return shippedDate
        ? `Shipped ${shippedDate.format('MMM D, dddd')}`
        : 'Shipped'
    default:
      return 'Ready to go'
  }
}
