import useDetectDevices from '../../hooks/useDetectDevices'
import { capitalize } from '../../utils'
import dayjs from 'dayjs'
import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../styles/utils'
import { StyledButton } from './commons'
import { getLastMileTrackingUrl } from '../../utils/tracking'

const StyledTrackingHistory = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(10)};
  }
`

const TrackingDetail = styled.p`
  line-height: 14px !important;
  p {
    line-height: 14px !important;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    line-height: ${desktopVw(18)} !important;
    p {
      line-height: ${desktopVw(18)} !important;
    }
  }
`

const TrackingLineItem = styled.div`
  display: flex;
  &:first-child {
    p {
      color: ${({ theme }) => theme.colors.primary};
      text-decoration: underline;
      text-transform: uppercase;
    }
    ${TrackingDetail} {
      font-weight: 500;
    }
  }
  &:last-child {
    border-bottom: none;
  }
  p {
    font-size: 11px;
    line-height: 13.84px;
    color: ${({ theme }) => theme.colors.darkGrey};
  }
  padding-bottom: 10px;
  padding-top: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    p {
      font-size: ${desktopVw(11)};
    }
  }
`

const TrackingText = styled.p`
  margin-right: 65px;
  text-transform: uppercase;
  text-decoration: none !important;
  p {
    width: 55px;
    text-decoration: none !important;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-basis: ${desktopVw(200)};
    flex-shrink: 0;
    p {
      width: unset;
      margin-right: ${desktopVw(6)};
      line-height: ${desktopVw(17.5)};
    }
  }
`

export function TrackingHistory({
  onClick,
  selectedShipment,
  trackId,
  trackingHistory,
}) {
  const { devices } = useDetectDevices()

  return (
    <StyledTrackingHistory>
      <TrackingLineItem>
        <TrackingText>
          <p>Shipping </p>
          <p>Activity</p>
        </TrackingText>
        <TrackingDetail>
          {devices.mobile && (
            <>
              <p style={{ fontWeight: 500 }}>Tracking ID</p>
              <a
                href={getLastMileTrackingUrl(
                  selectedShipment?.carrier,
                  selectedShipment?.trackingDetail,
                )}
                target='_blank'
                rel='noopener noreferrer'
              >
                {trackId}
              </a>
            </>
          )}
          {devices.desktop && (
            <a
              href={getLastMileTrackingUrl(
                selectedShipment?.carrier,
                selectedShipment?.trackingDetail,
              )}
              target='_blank'
              rel='noopener noreferrer'
              style={{ textTransform: 'capitalize', fontWeight: 500 }}
            >
              Tracking ID: {trackId}
            </a>
          )}
        </TrackingDetail>
      </TrackingLineItem>
      {trackingHistory.map(historyItem => {
        const dateObj = dayjs(historyItem.eventTime)
        const date = dateObj.format('MMM DD')
        const time = dateObj.format('h:mm A')
        return (
          <TrackingLineItem>
            <TrackingText>
              <p>{date}</p>
              <p>{time}</p>
            </TrackingText>
            <TrackingDetail>
              {historyItem.message && <p>{capitalize(historyItem.message)}</p>}
              {historyItem.location && <p>{historyItem.location}</p>}
            </TrackingDetail>
          </TrackingLineItem>
        )
      })}
      {(selectedShipment?.trackingHistory ?? []).length !==
        trackingHistory.length && (
        <StyledButton onClick={onClick}>
          See Previous Shipping History
        </StyledButton>
      )}
    </StyledTrackingHistory>
  )
}
