function getTrackingHistory(devices, selectedShipment) {
  return devices.mobile
    ? selectedShipment?.trackingHistory?.[0]
      ? [selectedShipment?.trackingHistory?.[0]]
      : []
    : selectedShipment?.trackingHistory ?? []
}

function isDeliveryDateBeforeToday(deliveredDateObj) {
  return deliveredDateObj.isBefore(dayjs(), 'D')
}

const Tracking = ({
  data: {
    contentfulPage: {
      imageWithTextSection: { image, imageDesktop, text, textMobile, button },
      bestSellers,
      sections,
    },
  },
  location,
}) => {
  const { devices } = useDetectDevices()
  const { fetchData, response: order, loading } = useFetch()
  const trackingSectionRef = useRef(null)

  const params = queryString.parse(location.search)

  const options = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
  }

  const oldUrlPattern = new RegExp(
    '\\?order=\\/([0-9a-zA-Z]*)\\?track-id=([0-9a-zA-Z]*)',
    'g',
  )

  const match = oldUrlPattern.exec(location.search)

  if (
    (!params?.orderId && !params?.order && !match?.[1]) ||
    (!params?.trackId &&
      !params?.['track-id'] &&
      !params?.shipmentId &&
      !match?.[2])
  )
    navigate(`/account/my-orders-returns`)

  const orderId = match?.[1] || params?.order || params?.orderId
  const trackId =
    params?.trackId ||
    params?.['track-id'] ||
    match?.[2] ||
    (order?.shipments ?? []).find(
      s => s.shipmentId === Number(params?.shipmentId),
    )?.trackingDetail?.carrierTrackingNumber

  useEffect(() => {
    const url = `${process.env.GATSBY_ORDERS_API_ENDPOINT}/service/order/${orderId}`
    if (orderId) fetchData(url, options)
  }, [])

  const selectedShipmentIndex = (order?.shipments ?? []).findIndex(
    s => s?.trackingDetail?.carrierTrackingNumber === trackId,
  )

  const selectedShipment = order?.shipments?.[selectedShipmentIndex]

  const [trackingHistory, setTrackingHistory] = useState(
    getTrackingHistory(devices, selectedShipment),
  )

  useEffect(() => {
    setTrackingHistory(getTrackingHistory(devices, selectedShipment))
  }, [selectedShipment])

  if (loading || !selectedShipment)
    return (
      <SpinnerWrapper>
        <DottedSpinner />
      </SpinnerWrapper>
    )

  const deliveredDateObj = selectedShipment?.statusSummary?.deliveryDate
    ? dayjs(selectedShipment?.statusSummary?.deliveryDate)
    : undefined

  // const isDelayed =
  //   isDeliveryDateBeforeToday(deliveredDateObj) &&
  //   selectedShipment?.statusSummary?.status !== 'order-delivered'

  // if (isDelayed) deliveredDateObj = dayjs(addWorkDays(new Date(), 5))

  const deliveryMonth = deliveredDateObj ? deliveredDateObj.format('MMM') : null
  const deliveryDate = deliveredDateObj ? deliveredDateObj.format('D') : null
  const deliveryDay = deliveredDateObj ? deliveredDateObj.format('dddd') : null

  return (
    <>
      {selectedShipment?.statusSummary?.deliveryDate && (
        <StickySection
          trackingSectionRef={trackingSectionRef}
          selectedShipment={selectedShipment}
          deliveredDateObj={deliveredDateObj}
        />
      )}
      <Container>
        <LinkWrapper>
          <Link to='/account/my-profile'>Account</Link>/
          <Link to='/account/my-orders-returns'>Orders</Link>/<p>{orderId}</p>/
          <p>Shipment {selectedShipmentIndex + 1}</p>
        </LinkWrapper>
        <Wrapper ref={trackingSectionRef}>
          <LeftContainer>
            <Header>
              <p>DELIVERY STATUS</p>
              <p>ORDER #{order?.orderNumber}</p>
            </Header>
            <Content>
              <ContentWrapper>
                {selectedShipment?.statusSummary?.deliveryDate ? (
                  <DateWrapper>
                    <Day>{deliveryDay}</Day>
                    <Month>{deliveryMonth}</Month>
                    <StyledDate>{deliveryDate}</StyledDate>
                    {selectedShipment?.statusSummary?.status !==
                      'order-delivered' && (
                      <Text
                        style={{
                          color: theme.colors.primary,
                        }}
                      >
                        Estimated delivery date:
                      </Text>
                    )}
                  </DateWrapper>
                ) : (
                  <DeliveryDateUnknowText>
                    {TRACKING_UNKNOWN_DATA_BY_CARRIER}
                  </DeliveryDateUnknowText>
                )}
                <div>
                  {devices.desktop ? (
                    <LineItemImageWrapper>
                      {(selectedShipment?.lineItems ?? []).map(lineItem => (
                        <Img
                          key={lineItem.id}
                          loading='lazy'
                          src={lineItem.image}
                          isLarge={selectedShipment?.lineItems.length <= 2}
                        />
                      ))}
                    </LineItemImageWrapper>
                  ) : (
                    <LineItemImageWrapper>
                      {selectedShipment?.lineItems.length > 4 ? (
                        <StyledCarousel
                          mobileSettings={mobileSettings}
                          desktopSettings={mobileSettings}
                        >
                          {(selectedShipment?.lineItems ?? []).map(lineItem => (
                            <Img
                              key={lineItem.id}
                              loading='lazy'
                              src={lineItem.image}
                              isLarge={selectedShipment?.lineItems.length <= 2}
                            />
                          ))}
                        </StyledCarousel>
                      ) : (
                        (selectedShipment?.lineItems ?? []).map(lineItem => (
                          <Img
                            key={lineItem.id}
                            loading='lazy'
                            src={lineItem.image}
                            isLarge={selectedShipment?.lineItems.length <= 2}
                          />
                        ))
                      )}
                    </LineItemImageWrapper>
                  )}
                </div>
              </ContentWrapper>
              <TimeLine
                selectedShipment={selectedShipment}
                shippedDate={
                  selectedShipment?.statusSummary?.pickupDate
                    ? dayjs(selectedShipment?.statusSummary?.pickupDate)
                    : null
                }
              />
              <TrackingHistory
                devices={devices}
                selectedShipment={selectedShipment}
                trackId={trackId}
                trackingHistory={trackingHistory}
                onClick={() => {
                  setTrackingHistory(selectedShipment?.trackingHistory ?? [])
                }}
              />
            </Content>
          </LeftContainer>
          <RightContainer>
            {devices.mobile && (
              <ImageWrapper>
                <ImageMobile fluid={image?.fluid || imageDesktop?.fluid} />
                <ImageContent>
                  <p>
                    {documentToReactComponents(textMobile?.json || text?.json)}
                  </p>
                  <StyledAdButton to={getLinkPath(button)} color>
                    {button?.text}
                  </StyledAdButton>
                </ImageContent>
              </ImageWrapper>
            )}
            {devices.desktop && (
              <DesktopImageWrapper>
                <ImageDesktop fluid={imageDesktop?.fluid || image?.fluid} />
                <TextBox>
                  <p>
                    {documentToReactComponents(text?.json || textMobile?.json)}
                  </p>
                  <StyledAdButton to={getLinkPath(button)}>
                    {button?.text}
                  </StyledAdButton>
                </TextBox>
              </DesktopImageWrapper>
            )}
          </RightContainer>
        </Wrapper>
        <FeedbackBox>
          <div>
            <FeedbackText style={{ marginBottom: 6 }}>
              HOW WAS YOUR SHIPPING EXPERIENCE?
            </FeedbackText>
            <SubText>Help us improve!</SubText>
          </div>
          <StarContainer>
            <StarRating
              onClick={value => {
                eventTracker.track(EventType.ENTER_FEEDBACK, {
                  action: 'tracking',
                  entity_type: 'rating',
                  value,
                  context: {
                    tracking_id: trackId,
                    order_id: orderId,
                    shipment_id: selectedShipment?.shipmentId,
                  },
                })
              }}
            />
          </StarContainer>
        </FeedbackBox>
        {bestSellers && (
          <StyledNewBestSellersSection
            json={bestSellers.text.json}
            textColor={bestSellers.textColor}
            image={bestSellers.image}
            products={bestSellers.products}
            tracker='tracking'
            hideImageOnMobile
          />
        )}
        <AdsSection sections={sections} />
      </Container>
    </>
  )
}

export default Tracking

export const TrackingQuery = graphql`
  query Tracking($id: String!) {
    contentfulPage(id: { eq: $id }) {
      id
      imageWithTextSection {
        ...ImageWithTextSection
      }
      sections {
        ... on ContentfulImageWithTextSection {
          ...ImageWithTextSection
        }
      }
      bestSellers {
        id
        text {
          json
        }
        products {
          id
          title
          seoTitle
          slug
          subtitle
          metaTags
          shippingInformation {
            shippingInformation
          }
          shippingMetaInfoText {
            shippingMetaInfoText
          }
          productImages {
            ...ProductImages
          }
          colors {
            ...Color
          }
          shopifyProduct {
            ...ShopifyProduct
          }
        }
      }
    }
  }
`
