import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import useScrollListener from '../../hooks/useScrollListener'
import { desktopVw } from '../../styles/utils'
import { colors } from '../../styles/theme'

const StyledSection = styled.div`
  opacity: var(--opacity);
  pointer-events: var(--pointerEvents);
  background: ${props => props.theme.colors.secondary};
  position: fixed;
  z-index: 1001;
  top: 59px;
  width: 100%;
  transition: 0.2s;
  padding: 12px 12px 10px 12px;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.1);
  border-top: 1px solid rgba(0, 0, 0, 0.1);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(78)};
    padding: ${desktopVw(20)} ${desktopVw(36)};
  }
`

const TitleText = styled.p`
  font-size: 27px;
  margin-bottom: 10px;
  font-family: ${({ theme }) => theme.fonts.secondary};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(40)};
    margin-bottom: ${desktopVw(10)};
  }
`

const LineItemImageWrapper = styled.div`
  margin-top: 13px;
`

const Img = styled.img`
  width: 49px;
  height: 64px;
  background-color: grey;
  margin-right: 13px;
  object-fit: cover;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(56)};
    height: ${desktopVw(75)};
  }
`

export const StickySection = ({
  trackingSectionRef,
  selectedShipment,
  deliveredDateObj,
}) => {
  const [isScrolling] = useScrollListener()
  const [isHeaderSticky, setHeaderSticky] = useState(false)
  const [lastScrollPosition, setLastScrollPosition] = useState(0)

  useEffect(() => {
    if (!(trackingSectionRef && trackingSectionRef.current)) return
    const buttonTop = trackingSectionRef.current.getBoundingClientRect().top
    const scrolledPastButton = buttonTop < 40
    if (window.pageYOffset >= lastScrollPosition) {
      setLastScrollPosition(window.pageYOffset)
      if (scrolledPastButton && !isHeaderSticky) {
        setHeaderSticky(true)
      } else if (!scrolledPastButton && isHeaderSticky) {
        setHeaderSticky(false)
      }
    } else {
      setLastScrollPosition(window.pageYOffset)
      if (isHeaderSticky) {
        setHeaderSticky(false)
      }
    }
  }, [isScrolling])

  return (
    <StyledSection
      style={{
        '--opacity': isHeaderSticky ? 1 : 0,
        '--pointerEvents': isHeaderSticky ? 'auto' : 'none',
      }}
    >
      <TitleText
        style={{
          color:
            selectedShipment?.statusSummary?.status === 'order-delivered'
              ? colors.shamrockGreen
              : colors.primary,
        }}
      >
        {`${
          selectedShipment?.statusSummary?.status === 'order-delivered'
            ? 'Delivered:'
            : 'Delivery by:'
        } ${deliveredDateObj.format('MMM D dddd')}`}
      </TitleText>
      <LineItemImageWrapper>
        {(selectedShipment?.lineItems ?? []).map(lineItem => (
          <Img key={lineItem.id} loading='lazy' src={lineItem.image} />
        ))}
      </LineItemImageWrapper>
    </StyledSection>
  )
}
