import styled from 'styled-components'
import Image from 'gatsby-image'
import Button from '../Button'
import { desktopVw, mobileVw } from '../../styles/utils'

export const StyledButton = styled(Button)`
  text-transform: uppercase;
  font-size: 9px;
  padding: 10px 40px;
  width: 100%;
  color: ${({ theme }) => theme.colors.secondary};
  background: ${({ theme }) => theme.colors.primary};
  font-family: ${({ theme }) => theme.fonts.heading};
`
export const StyledAdButton = styled(StyledButton)`
  margin-top: 5px;
  width: 89px;
  padding: 10px 0;
  font-family: ${({ theme }) => theme.fonts.primary};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
    width: ${desktopVw(137)};
  }
`
export const ImageMobile = styled(Image)`
  height: 100%;
`
export const ImageWrapper = styled.div`
  position: relative;
  height: ${mobileVw(183)};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(307)};
  }
`
